#footerLink:hover{
    text-decoration: none;
    color: white;
    transition: 0.8s cubic-bezier(0.2, 0.8, 0.2, 1.0);
    transition-property: all;
   
}
#footerLink{
    color: #6c757d;
    transition: 0.2s cubic-bezier(0.2, 0.8, 0.2, 1.0);
    transition-property: all;
    
}
#entireFooter{
    border-top: black;
    border-top-style: solid;
    border-top-width: 1px;
}