#gearLogo2 {
  position: absolute;
  top: -30px;
  -webkit-animation: infinite-spinning-back 2s 0s infinite linear;
          animation: infinite-spinning-back 2s 0s infinite linear;
}
#gearLogo {
  width: 100%;
  position: absolute; /* 2 */
  top: 0%; /* 3 */
  -webkit-animation: infinite-spinning 2s 0s infinite linear;
          animation: infinite-spinning 2s 0s infinite linear;
}
@-webkit-keyframes infinite-spinning {
  from {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}
@keyframes infinite-spinning {
  from {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}
@-webkit-keyframes infinite-spinning-back {
  from {
    -webkit-transform: rotate(-360deg);
            transform: rotate(-360deg);
  }
}
@keyframes infinite-spinning-back {
  from {
    -webkit-transform: rotate(-360deg);
            transform: rotate(-360deg);
  }
}
